
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API,getQuery } from '../../services';

export default {
components: {
Scroller,
Spinner,
XButton,
Group,
Cell,
LoadMore,
Toast
},
data () {
return {
paydata:{},
confrim:"",
toast:false,
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
currentType:0,/* 0-6分辨对应6种状态 */
/* 订单状态 */
typeList:[{
name:"全部订单",
type:0,
},{
name:"待付款",
type:1,
},{
name:"待发货",
type:2,
},{
name:"待收货",
type:3,
},{
name:"待归还",
type:4,
},{
name:"待结算",
type:5,
},{
name:"待评价",
type:6,
},],
/* 时间对照表 */
timeMap:{1:"日",2:"周",3:"月",4:"季",5:"年"},
/* 假数据模拟订单 */
orderList:[],
currentPage:1,
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),

},
mounted () {
localStorage.setItem("orderScroll",0);
this.currentPage=1;
this.getTypeData();
setInterval(()=>{
if(localStorage.getItem("reload")){
this.currentPage=1;
this.getTypeData();
localStorage.setItem("reload","");
}
},1000);
},
activated(){
setTimeout(()=>{
if(localStorage.getItem("orderScroll")){
document.querySelector(".order_list").scrollTop=localStorage.getItem("orderScroll");
}
},100);
},
methods: {
/* typelist点击选中函数 */
typeselect(index){
this.currentType=index;
},
/* 进入订单详情 */
getInfo(id){
window.location.href="/#/orderAction/"+id;
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
},
/* 确认订单状态 */
confrimType(item){
if (item.order_is_delete == 0) {
if (item.order_refund_goods_status == 0) {
if (item.order_status == 1) {
if(item.order_shipping_status == 8){
item.orderType=8;//订单关闭
}else{
item.orderType=1;//代付款
}
} else if (item.order_status == 2) {
switch (item.order_shipping_status) {
case 1:
item.orderType=2;//代发货
break;
case 2:
item.orderType=3;//待收货
break;
case 3:
item.orderType=4;//待归还
break;
case 4:
item.orderType=5;//待结算
break;
case 5:
item.orderType=5;//待确认结算
item.srcorderType=2;
break;
case 6:
item.orderType=6;//待评价
break;
case 7:
item.orderType=7;//评价完成
break;
}
}
} else if (item.order_refund_goods_status == 1) {
item.orderType=9;//退款结束
} else if (item.order_refund_goods_status == 2) {
if (item.order_status == 2) {//退款退货
if (item.order_shipping_status == 2) {
item.srcorderType=0;//退货待结算
} else if (item.order_shipping_status == 3) {
item.srcorderType=0;//退货待结算
} else if (item.order_shipping_status == 5) {
item.srcorderType=1;//退货待确认结算
} else if (item.order_shipping_status == 6) {
item.orderType=10;//退货完成
}
}
} else if (item.order_refund_goods_status == 4) {
if (item.order_shipping_status == 6) {
item.orderType=10;//退货完成
}
}else if(item.order_refund_goods_status == 3){
item.srcOrderType=3;//退款处理中
}
}
},
/* 获取对应的数据 */
getTypeData(){
if(this.currentPage==1){
this.orderList=[];
}
API.order.orderlist({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderStatus:0,
page_number:10,
page:this.currentPage,
}).then((res)=>{
if(res.body.code==200){
let list= res.body.data.orderList.data
for(let item of list) {
this.confrimType(item);
this.orderList.push(item);
}
if(list.length==10){
console.log(1);
this.currentPage++;
this.getTypeData();
}else{

return false;
}
/* this.orderList=list; */
}
});
},
/* 删除订单 */
deletOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要删除该订单吗',
onConfirm () {
/* 点击确认时执行具体删除操作 */
API.order.orderDel({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="删除成功";
self.toast=true;
self.currentPage=1;
self.getTypeData();
}
});
}
});

},
/* 支付接口 */
onBridgeReady(){
let self=this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest',self.paydata,
function(res){
if(res.err_msg =="get_brand_wcpay_request:fail")  {
alert(JSON.stringify(self.paydata));
alert(JSON.stringify(res));
}
if(res.err_msg == "get_brand_wcpay_request:ok" ) {
self.confrim="支付成功";
self.toast=true;
self.currentPage=1;
self.getTypeData();
}     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
}
);
},
/* 订单付款 */
payOrder(id){
/* 待付款订单生成支付订单 */
let self=this;
API.order.orderShipPay({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderId:id,
}).then((res)=>{
let openId=localStorage.getItem("openId");
API.order.OrderWechat({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderSn:res.body.data.order_big_sn,
payMethod:3,
openId:openId,
}).then((resopndy)=>{
this.paydata=resopndy.body;
console.log(resopndy.body);
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
}else{
self.onBridgeReady();
}
},(err)=>{
alert(JSON.stringify(err));
});
})

},
/* 确认收货 */
confrimOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认收货',
onConfirm () {
/* 点击确认时执行具体删除操作 */
API.order.orderReceipt({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="确认收货成功";
self.currentPage=1;
self.getTypeData();
self.toast=true;
}
});
}
});
},
/* 确认归还 */
confirmReturn(item){
/* * sinceId 自提点编号
* expressId 物流单号
* logisticsName 物流公司简拼
* revertId 归还地址编号
* company 物流公司名 */
let self=this;
if(item.order_express_type==2){
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认归还',
onConfirm () {
/* 点击确认时执行具体删除操作 */
API.order.orderReturn({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:item.order_id,
sinceId:"",
expressId:"",
logisticsName:"",
revertId:"",
company:"",
}).then((res)=>{
if(res.body.code==200){
self.confrim="归还成功";
self.currentPage=1;
self.getTypeData();
self.toast=true;
}
});
}
});
}else{
console.log(item);
this.$router.push({ path: '/orderReturn?type='+item.order_express_type+"&orderId="+item.order_id});
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
}

},
/* 确认结算 */
confrimSettlement(id){
this.$router.push({path:'/settlement',query:{
type:1,
id:id,
}});
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
},
/* 查看结算单 */
seeSettlement(id){
window.location.href="/#/settlement?id="+id;
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
},
/* 提醒发货 ,结算 1-提醒发货2-提醒结算*/
remind(id,type){
let self=this;
API.order.orderRemind({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
type:type,
}).then((res)=>{
if(res.body.code==200){
if(type==1){
self.confrim="提醒发货成功";
}else{
self.confrim="提醒结算成功";
}
self.currentPage=1;
self.toast=true;
}
});
},
/* 查看物流,评价, */
download(id){
window.location.href='/#/download';
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
}
}
}






