
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
Toast,
ListCompent
},
data () {
return {
confrim:"请选择地址",
toast:false,
storeName:null,
storeFace:null,
storebgPic:null,
storeId:null,
goodsList:[1,5,7,6,8],
cardList:[]
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
},
methods:{
routerBack(){
this.$router.goBack();
},
/* 领取店铺优惠券 */
getCard(id){
if(!this.getUserInfoUserId){
this.$router.push({
path:"/login",
});
return false;
}
API.card.receiveStoreCoupon({
storeId:this.storeId,
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
couponNo:id
}).then((res)=>{
if(res.body.code==200){
this.confrim="领取成功";
this.toast=true;
}else{
this.confrim=res.body.msg;
this.toast=true;
}
})
},
/* 跳到店铺详情页面 */
goInfo(){
window.location.href="/#/shopInfo/"+this.storeId;
},
/* 数据初始化 */
Initialization(){
this.storeId=this.$route.params.id;
API.main.storeInfo({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.storeName=res.body.data.storeName;
this.storeFace=res.body.data.storeFace;
this.storebgPic=res.body.data.storebgPic;
}
});
API.main.storeGoods({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.goodsList=res.body.data.shopList.data;
}
});
API.card.stordCardList({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.cardList=res.body.data;
}
})
}
},mounted(){
this.Initialization();
},
activated(){
this.Initialization();
}
}



